'use client'
import { toast } from 'sonner'
import { useEffect, useState } from 'react'
import { ethers, type JsonRpcSigner, type BrowserProvider } from 'ethers'

export default function useWallet() {
  const [account, setAccount] = useState('')
  const [chainId, setChainId] = useState<number | null>(null)
  const [signer, setSigner] = useState<JsonRpcSigner | null>(null)
  const [provider, setProvider] = useState<BrowserProvider | null>(null)
  const [accounts, setAccounts] = useState<string[]>([])
  const isConnected = Boolean(account && provider && signer)

  //* --- è¿žæŽ¥é’±åŒ…å’Œåˆå§‹åŒ– provider å’Œ signer ---
  const connect = async () => {
    if (typeof window.ethereum === 'undefined') {
      toast.error('è¯·å…ˆå®‰è£… MetaMask é’±åŒ…æˆ–å…¶ä»–ä»¥å¤ªåŠé’±åŒ…æ‰©å±•ï¼ŒåŽå†åˆ·æ–°é¡µé¢')
      return false
    }

    try {
      // è¿žæŽ¥é’±åŒ…
      const nextAccounts: string[] = await window.ethereum.request({
        method: 'eth_requestAccounts',
      })

      if (!nextAccounts.length) {
        throw new Error('ç”¨æˆ·æ‹’ç»äº†è¿žæŽ¥è¯·æ±‚')
      }

      setAccounts(nextAccounts)
      await initializeProviderAndSigner(nextAccounts[0])
      setAccount(nextAccounts[0])
      return true
    } catch (err: any) {
      console.error('è¿žæŽ¥é’±åŒ…å¤±è´¥', err)
      toast.error(err.message)
      return false
    }
  }

  const disconnect = () => {
    setAccount('')
    setChainId(null)
    setSigner(null)
    setProvider(null)
    setAccounts([])
    window.ethereum?.removeListener('accountsChanged', handleAccountsChanged)
    window.ethereum?.removeListener('chainChanged', handleChainChanged)
  }

  const initializeProviderAndSigner = async (accountAddress: string) => {
    if (!window.ethereum) return

    const web3Provider = new ethers.BrowserProvider(window.ethereum)
    const network = await web3Provider.getNetwork()
    const web3Signer = await web3Provider.getSigner()

    setSigner(web3Signer)
    setProvider(web3Provider)
    setAccount(accountAddress)
    setChainId(Number(network.chainId))
  }

  //* --- å¤„ç†è´¦æˆ·å˜æ›´ ---
  //* è¿žæŽ¥è´¦æˆ·å˜æ›´ï¼ˆEIP-1193 ä¼šä¼ å…¥è´¦æˆ·æ•°ç»„ï¼‰
  const handleAccountsChanged = (eventAccounts: string[]) => {
    if (!eventAccounts || eventAccounts.length === 0) {
      disconnect()
      return
    }
    const nextAccount = eventAccounts[0]
    setAccounts(eventAccounts)
    setAccount(nextAccount)
    initializeProviderAndSigner(nextAccount)
  }

  //* è¿žæŽ¥ç½‘ç»œå˜æ›´ï¼ˆEIP-1193 ä»¥ 0x å¼€å¤´çš„åå…­è¿›åˆ¶å­—ç¬¦ä¸²ä¼ å…¥ï¼‰
  const handleChainChanged = async (chainIdHex: string) => {
    // å°† 0x åå…­è¿›åˆ¶é“¾ ID è½¬ä¸ºåè¿›åˆ¶æ•°å­—
    const nextChainId = Number.parseInt(chainIdHex, 16)
    setChainId(nextChainId)
    console.log('è¿žæŽ¥ç½‘ç»œå˜æ›´', chainIdHex, nextChainId)
    // é‡æ–°åˆå§‹åŒ– provider/signer ä»¥ç»‘å®šåˆ°æ–°ç½‘ç»œ
    if (account) {
      await initializeProviderAndSigner(account)
    }
    toast.success(`å·²è¿žæŽ¥åˆ°ç½‘ç»œ: ${nextChainId}`)
  }

  //* --- åˆ‡æ¢ç½‘ç»œ ---
  const switchNetwork = async (targetChainId: number) => {
    if (!window.ethereum) {
      toast.error('æœªæ£€æµ‹åˆ°ä»¥å¤ªåŠé’±åŒ…')
      return false
    }
    try {
      await window.ethereum.request({
        method: 'wallet_switchEthereumChain',
        params: [{ chainId: `0x${targetChainId.toString(16)}` }],
      })
      return true
    } catch (err: any) {
      // 4902: Unrecognized chain â€“ éœ€è¦å…ˆæ·»åŠ ç½‘ç»œ
      if (err?.code === 4902) {
        toast.error('å½“å‰é’±åŒ…æœªæ·»åŠ è¯¥ç½‘ç»œï¼Œè¯·å…ˆåœ¨é’±åŒ…ä¸­æ·»åŠ ')
      } else {
        toast.error(err?.message || 'åˆ‡æ¢ç½‘ç»œå¤±è´¥')
      }
      return false
    }
  }

  //* --- æ£€æŸ¥é’±åŒ…è¿žæŽ¥çŠ¶æ€ ---
  const checkIsWalletConnected = async () => {
    if (typeof window.ethereum === 'undefined') {
      toast.error('è¯·å…ˆå®‰è£… MetaMask é’±åŒ…ï¼ŒåŽå†åˆ·æ–°é¡µé¢')
      return
    }

    try {
      const existingAccounts: string[] = await window.ethereum.request({
        method: 'eth_accounts',
      })

      if (existingAccounts.length > 0) {
        setAccounts(existingAccounts)
        await initializeProviderAndSigner(existingAccounts[0])
      }
    } catch (err) {
      console.error('æ£€æŸ¥é’±åŒ…è¿žæŽ¥çŠ¶æ€å¤±è´¥', err)
      toast.error('æ£€æŸ¥é’±åŒ…è¿žæŽ¥çŠ¶æ€å¤±è´¥')
    }
  }

  //* --- æ‰‹åŠ¨é€‰æ‹©å½“å‰è´¦æˆ· ---
  const selectAccount = async (address: string) => {
    if (!window.ethereum) return false
    // æ›´æ–°æœ¬åœ°é€‰æ‹©ï¼Œprovider/signer ä¼šåŸºäºŽå½“å‰è´¦å·é‡æ–°èŽ·å–
    if (!accounts.includes(address)) return false
    setAccount(address)
    await initializeProviderAndSigner(address)
    return true
  }

  useEffect(() => {
    checkIsWalletConnected()
    if (!window.ethereum) return
    window.ethereum.on('accountsChanged', handleAccountsChanged)
    window.ethereum.on('chainChanged', handleChainChanged)
    return () => {
      window.ethereum?.removeListener('accountsChanged', handleAccountsChanged)
      window.ethereum?.removeListener('chainChanged', handleChainChanged)
    }
  }, [])

  return {
    signer,
    account,
    chainId,
    provider,
    accounts,
    isConnected,
    connect,
    disconnect,
    switchNetwork,
    selectAccount,
  }
}
